import { Component, inject } from '@angular/core';
import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { ListaUsuariosComponent} from "../lista-usuarios/lista-usuarios";
import { AdicionarUsuariosComponent } from "../adicionar-usuarios/adicionar-usuarios";
import { RouterModule,  } from "@angular/router";
import { Router, RouterOutlet } from '@angular/router';
import { CommonModule } from '@angular/common';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrl: './home.component.css',
  imports: [
    MatToolbarModule,
    MatButtonModule,
    MatIconModule,
    RouterOutlet,
    RouterModule,
    CommonModule
]
})
export class HomeComponent {
  private breakpointObserver = inject(BreakpointObserver);

  
  constructor(private router: Router) {}

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
    .pipe(
      map(result => result.matches),
      shareReplay()
    );
  itensMenu =[
    {label: 'home', link: HomeComponent},
    {label: 'adicionar-usuarios', route: AdicionarUsuariosComponent},
    {label: 'listar-usuario', route: ListaUsuariosComponent},
  ]

adicionarUsuario() {
    this.router.navigate(['/adicionar']);
  }

}
