// src/app/components/adicionar-usuarios/adicionar-usuarios.ts
import { Component, OnInit, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormBuilder, Validators } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute, Router, RouterOutlet } from '@angular/router';

@Component({
  selector: 'app-adicionar-usuarios',
  standalone: true,
  imports: [CommonModule, ReactiveFormsModule, RouterOutlet],
  templateUrl: './adicionar-usuarios.html',
  styleUrls: ['./adicionar-usuarios.css']
})
export class AdicionarUsuariosComponent implements OnInit {
  private readonly API = 'http://localhost:3000/usuarios';

  // Injeções com `inject()`
  private fb = inject(FormBuilder);
  private http = inject(HttpClient);
  public router = inject(Router);
  private route = inject(ActivatedRoute);

  // Agora `fb` já existe quando este campo é avaliado
  form = this.fb.group({
    id: this.fb.control<number | null>(null),
    nome: this.fb.nonNullable.control('', [
      Validators.required,
      Validators.minLength(3),
      Validators.pattern('^[A-Za-zÀ-ÖØ-öø-ÿ ]+$') // só letras e espaços
    ]),
    email: this.fb.nonNullable.control('', [Validators.required, Validators.email]),
    // opcional: number | null com validação de mínimo
    idade: this.fb.control<number | null>(null, { validators: [Validators.min(18)] })
  });

  idEdicao?: String;

  ngOnInit() {
    const id = this.route.snapshot.paramMap.get('id');
    this.idEdicao = id ? String(id) : undefined;

    if (this.idEdicao) {
      this.http.get<any>(`${this.API}/${this.idEdicao}`)
        .subscribe(usuario => {
          this.form.patchValue({
            nome: usuario?.nome ?? '',
            email: usuario?.email ?? '',
            idade: usuario?.idade ?? null
          });
        });
    }
  }

  salvar() {
    if (this.form.invalid) {
      this.form.markAllAsTouched();
      return;
    }
    const payload = this.form.value;
    const req$ = this.idEdicao
      ? this.http.put(`${this.API}/${this.idEdicao}`, payload)
      : this.http.post(this.API, payload);

    req$.subscribe(() => this.router.navigate(['/usuarios']));
  }

  menuInicial() {
    this.router.navigate(['/home']);
  }
}
